import React, { Component } from 'react'
import {Link} from "react-router-dom"
export class navbar extends Component {
    render() {
        return (
            <div className="navbar navbar-expand-md navbar-dark bg-light" id="navbar">
        <div className="navbar-brand">
          <Link to="/portfolio"><img className="logo rounded-circle" src={this.props.user.logo} alt="logo" /></Link>
          <h1 className="navbar-brand ml-3" style={{color:"white"}}>{this.props.user.name}</h1>
        </div>
        <ul className="navbar-nav w-100 float-right">
          <li className="navbar-item">
            <Link className=" col-2 nav-link lin" to="/portfolio/projects">
              Projects
            </Link>
          </li>
          <li className="navbar-item">
            <Link className="nav-link lin" to="/portfolio/resume">
              Resume
            </Link>
          </li>
          <li className="navbar-item">
            <Link className="nav-link lin" to="/portfolio/videos">
              Videos
            </Link>
          </li>
          <li className="navbar-item">
            <Link className="nav-link lin" to="/portfolio/about">
              About
            </Link>
          </li>
          <li className="navbar-item">
            <Link className="nav-link lin" to="/portfolio/contact">
              Contact Us
            </Link>
          </li>
        </ul>
      </div>
        )
    }
}

export default navbar
